import React from "react";
import "../Styles/headerformat.css";
function Jumbotron() {
    const images = [
        "images/taz.jpg",
        "images/sylvester.jpg",
        "images/granny.jpg",
        "images/tweetybird.png",
        "images/elmerfudd.jpg",
        "images/bugsbunny.jpg", 
        "images/looneytoonsbackground.jpg",
        "images/daffyduck.png",
        "images/foghornleghorn.png",
        "images/porkypig.png",
        "images/roadrunner.jpg",
        "images/wileecoyote.jpg",
        "images/yosemitesam.png"
    ];
    
    return (
        <div className="jumbotron jumbotron-fluid" id="bgJumbo">
            <img src={images[0]}  alt="taz" className="formatPics"></img>
            <img src= {images[1]} alt="sylvesterpig" className="formatPics"></img>
            <img src= {images[2]} alt="granny" className="formatPics"></img>
            <img src= {images[3]} alt="tweetybird" className="formatPics"></img>
            <img src= {images[4]} alt="elmerfudd" className="formatPics"></img>
            <img src= {images[5]} alt="bugsbunny" className="formatPics"></img>
            <img src= {images[6]} alt="looneytoons" width="300" height="200"></img>
            <img src= {images[7]} alt="daffyduck" className="formatPics"></img>
            <img src= {images[8]} alt="foghornleghorn" className="formatPics"></img>
            <img src= {images[9]} alt="porkypig" className="formatPics"></img>
            <img src= {images[10]} alt="roadrunner" className="formatPics"></img>
            <img src= {images[11]} alt="wileecoyote" className="formatPics"></img>
            <img src= {images[12]} alt="yosemitesam" className="formatPics"></img>
        </div>
    )
};

export default Jumbotron;
